import { Component } from '@angular/core';
import { slide} from './../animation/animate';
import {fadeAnimation} from './../animation/animate';
import { trigger, transition, style, animate, keyframes, animation, useAnimation, query, animateChild, group, stagger } from '@angular/animations';


@Component({
  selector: 'todos',
  templateUrl: './todos.component.html',
  styleUrls: ['./todos.component.css'],
  animations:[
    trigger('todoAnimate',[
      transition(':enter',[
        group([
          query('h1',[style({
            transform:'translateY(-20px)'}),animate(700)]),query('@fAnimate',stagger(400,animateChild()) )
        ])
      ])
      ]), trigger('fAnimate',[
          transition(':enter',useAnimation(fadeAnimation,{
          params:{duration:'2s',easing:'ease-in-out'}
          })),transition(':leave',[
        animate(500, style({opacity:0,transform:'translateX(30px)'}))
      ])
  ])
  ]
})

/*   transition('void => *',[
      style({backgroundColor:'yellow',opacity:1}),
      animate(2000,style({backgroundColor:"blueviolet",opacity:1,color:"#FFF"}))
    ])
    ,transition('* => void',[
      animate(1000,style({opacity:0}))
    ])   */
export class TodosComponent{

  constructor() { }


  items :string[]=['itshare c#','itshare JAVA' ,'itshare Angular','itshare php'];

  addItem(itemInput){
    this.items.push(itemInput.value);
    itemInput.value='';
  }
  removeItem(i){
    let index = this.items.indexOf(i);
    this.items.splice(index,1);
  }
fstart(event){
  console.log(event);
}
fend(e){
  console.log(e);
}


}
