import { Component,Input } from '@angular/core';
import { expandedCollapse } from './zippy.animation';


@Component({
  selector: 'address-collapse',
  templateUrl: './address-collapse.component.html',
  styleUrls: ['./address-collapse.component.css'],
  animations:[expandedCollapse]
})
export class AddressCollapseComponent {

  constructor() { }

  @Input('title') title:string;
  isExpanded :boolean;
  toggle()
  {
    this.isExpanded =!this.isExpanded;
  }
 
}
