import { trigger, transition, style, animate, keyframes, animation, useAnimation } from '@angular/animations';


export let bouncOutLift = animation([
  animate('2s ease-out',keyframes([
    style({
      offset:.5,
      opacity:1,
      backgroundColor:'#DDD',
      transform:'translateX(50px)'
    }),
    style({
      offset:.7,
      opacity:0,
      backgroundColor:'#DDD',
      transform:'translateX(-20px)'
    })
  ]))
])
export let slide = trigger('slide',[
    transition(':enter',[ 
        style({transform:'translateX(-20px)'}),
        animate(400)
    ]),transition(':leave',[
        // animate('0.5s 0s cubic-bezier(.26,.64,.84,-0.1)',style({transform:'translateX(150px)',opacity:0}))
       useAnimation(bouncOutLift) 
    ])
 
  ])


export let fade = trigger('fade',[
    transition(':enter, :leave',[ //=void <=> *
      animate(1000,style({opacity:0}))
    ])
 
  ])

  export let fadeAnimation = animation([
    style({opacity:.5,backgroundColor:'blueviolet'}),
    animate('{{duration}} {{easing}}')
  ],{
    params:{duration:'500ms',easing:'ease-in'}
  })